import React from 'react'

class TestPage extends React.Component {
  color = "red"

  static size = '1'

  render() {
    return (
      <div>
        <h1>Test...</h1>
        <button onClick={this._click1}>button1</button>
        <br/>
        <button onClick={this._click2}>button2</button>
      </div>
    )
  }

  _click1() {
    console.log('button1 -> this: ', this)
  }

  _click2 = () => {
    console.log('button2 -> this', this)
    console.log('displayName: ', TestPage.displayName)
    console.log('name: ', TestPage.name)
  }
}

export default TestPage